import { Table } from '@ali/4ever-plugin-table';
import { Document } from '@ali/4ever-cangjie';

export function isColumnsTable(node: any): node is Table {
  return Boolean(Table.isTable(node) && node.data.sr);
}

export function isColumnsRow(document: Document, key = '') {
  const parent = document.getParent(key);
  return Boolean(Table.isTable(parent) && parent.data.sr);
}

export function isColumnsCell(document: Document, key: string) {
  const parent = document.getParent(key);
  return isColumnsRow(document, parent?.key);
}
